<template>
  <div class="box">
    <div
      v-if="certificate?.[0] && formAnswers?.[0]"
      class="flex row ra"
    >
      <button
        class="button is-primary"
        type="button"
        onclick="window.print()"
      >
        <span class="icon"><i class="fas fa-print" /></span>
        <span>Imprimir</span>
      </button>
    </div>
    <div
      v-if="certificate?.[0] && formAnswers?.[0]"
      id="printarea"
    >
      <div
        v-for="formAnswer in formAnswers"
        :key="formAnswer.id"
        class="flex my-column sb"
      >
        <div class="flex my-column vca">
          <div
            id="header-area"
            class="flex row sb width-100"
          >
            <div
              style="width: 120px;"
              class="is-hidden"
            />
            <img
              src="@/assets/ualg.svg"
              style="width: 100mm"
            >
            <div
              id="qrcode"
              class="flex my-column ca vca is-hidden"
            >
              <vue-qrcode
                :value="'http://192.168.1.64:8000/forms/certificate/?cert='+formAnswer.id+'&auth='+formAnswer.authentication_key"
                :options="{ width: 120 }"
              />
              <span
                style="max-width: 120px"
                class="is-size-7"
              >Autenticar Certificado</span>
            </div>
          </div>
          <div class="spacer" />
          <h1 class="is-size-2">
            {{ certificate[0].title }}
          </h1>
          <div class="half-spacer" />
          <h2 class="is-size-4">
            {{ certificate[0].subtitle }}
          </h2>
          <h1 class="is-size-3">
            {{ formAnswer.author_first_name }} {{ formAnswer.author_last_name }}
          </h1>
          <div class="half-spacer" />
          <span>{{ certificate[0].text }}</span>
        </div>
        <div class="flex my-column vca date">
          <span>{{ certificate[0].date }}</span>
          <div class="spacer" />
          <div
            class="flex row sa"
            style="width: 100%;"
          >
            <div
              v-if="certificate[0].signature1 || certificate[0].signatureImage1"
              class="flex my-column vca"
            >
              <img
                :src="certificate[0].signatureImage1"
                style="height: 3cm;"
              >
              <span>{{ certificate[0].signature1 }}</span>
            </div>
            <div
              v-if="certificate[0].signature2 || certificate[0].signatureImage2"
              class="flex my-column"
            >
              <img
                :src="certificate[0].signatureImage2"
                style="height: 3cm;"
              >
              <span>{{ certificate[0].signature2 }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div 
      v-else-if="certificate && !certificate?.[0]"
      class="flex column ca vca"
      style="height: 50vh;"
    >
      <span class="title">Este certificado ainda não foi emitido</span>
    </div>
    <div 
      v-else-if="formAnswers && !formAnswers?.[0]"
      class="flex column ca vca"
      style="height: 50vh;"
    >
      <span class="title">Certificado inválido ou indisponível</span>
    </div>
    <div
      v-else
      class="flex column ca vca"
      style="height: 50vh;"
    >
      <span class="title mb-4">A validar certificado</span>
      <span
        class="loader mt-4"
        style="width: 100px; height: 100px;"
      />
    </div>
  </div>
</template>

<script>
import { API } from "@/http-common";
import VueQrcode from '@chenfengyuan/vue-qrcode';

export default {
  name: "ChooseForm",
  components: {VueQrcode},
  data() {
    return {
      formAnswers : undefined,
      certificate : undefined,
      event : undefined,
      certificate_id : this.$route.query.cert,
      auth : this.$route.query.auth
    }
  },
  created() {
    document.title = "Certificado de Participação"
    if (this.$route.query.event) {
      API.get("certificate/?event=".concat(this.$route.query.event))
        .then((response) => {this.certificate = response.data; if (!this.certificate?.[0].available) this.certificate = [];}
      );
      API.get("event/".concat(this.$route.query.event))
        .then((response) => {
          this.event = response.data;
          API.get("formAnswerNoAuthAuthor/?event=".concat(this.$route.query.event)
            .concat("&form=").concat(response.data.formsignupid)
            .concat("&status=4")
          )
            .then((secondResponse) => {
              this.formAnswers = secondResponse.data;
            }
          ).catch(() => this.formAnswers = []);
        }
      )
      .catch(() => this.formAnswers = []);
    }
    else if (this.$route.query.auth && this.$route.query.cert){
      API.get("formAnswerAuthor/".concat(this.$route.query.cert).concat("/?auth=").concat(this.$route.query.auth))
        .then((response) => {        
          this.formAnswers = [response.data];
          if (Array.isArray(this.formAnswers) && this.formAnswers.length == 0)
            return;
          API.get("certificate/?event=".concat(this.formAnswers[0].event))
          .then((response) => {
            this.certificate = response.data;
            if (!this.certificate?.[0].available) this.certificate = [];
          }
          ).catch(() => this.formAnswers = []);
        }
      ).catch(() => this.formAnswers = []);
    }
    else {
      this.formAnswers = [];
    }
  }
};
</script>

<style lang="scss">
  @import url(//db.onlinewebfonts.com/c/3b0ae6af94805b08b11cdf1d97c47dce?family=PreloSlabW01-Medium);

  #printarea>div {
    padding: 1cm;
  }
  #header-area {
    padding-left: 2mm;
    padding-right: 2mm;
  }

  @media print {
    .is-hidden-mobile, .is-hidden {
      display: flex !important;
    }

    .sb {
      justify-content: space-between !important;
    }

    #page-content {
      margin: 0;
    }
    #page-content * {
      visibility: hidden;
    }
    #printarea, #printarea * {
      visibility: visible;
    }

    #printarea {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }

    #printarea>div {
      height: 100vh;
      overflow: hidden;
      margin: 0;
      padding: 0;
    }

    #msg, #main-navbar, #top-hero, footer {
      display: none;
    }

    .date {
      margin-top: 0;
    }
  }

  #printarea {
    background-color: white;
  }
  h1, h2, h3, span {
    max-width: 20cm;
    text-align: center;
  }
  @page {
    size: A4 auto;
  }

  .date {
    margin-top: 1cm;
  }
  
  h1, h2, h3 {
    margin: 3mm;
  }

  .half-spacer {
    height: 5mm;
  }

  .spacer {
    height: 1cm;
  }

  #printarea * {
    box-sizing: border-box;
    font-family: "PreloSlabW01-Medium";
    page-break-after: avoid;
  }

  .flex {
    display: flex;
  }

  .row {
    flex-direction: row;
  }

  .my-column {
    flex-direction: column;
  }

  .ca {
    justify-content: center;
  }

  .sb {
    justify-content: center;
  }

  .sa {
    justify-content: space-around;
  }

  .vca {
    align-items: center;
  }
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>